import { useState, useEffect } from 'react';
import { Route, Routes, useLocation } from 'react-router-dom';
import NavBar from './components/NavBar/NavBar';
import AuthPage from './pages/AuthPage/AuthPage';
import HomePage from './pages/HomePage/HomePage';
import ForgotPassword from './pages/ForgotPassword/ForgotPassword';
import ProfilePage from './pages/ProfilePage/ProfilePage';
import styles from './App.module.scss';

export default function App() {
    const [user, setUser] = useState(null);
    const [post, setPost] = useState(null);
    const [token, setToken] = useState('');
    const location = useLocation();
    const shouldNotDisplayNavBar = !['/auth', '/auth/forgot-password'].includes(location.pathname);

    const signUp = async (credentials) => {
        try {
            const response = await fetch('/api/users', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(credentials)
            });
            const data = await response.json();
            setUser(data.user);
            setToken(data.token);
            localStorage.setItem('token', data.token);
            localStorage.setItem('user', JSON.stringify(data.user));
        } catch (error) {
            console.error(error);
        }
    };

    const login = async (credentials) => {
        try {
            const response = await fetch('/api/users/login', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(credentials)
            });
            const data = await response.json();
            const tokenData = data.token;
            localStorage.setItem('token', tokenData);
            const userData = data.user;
            localStorage.setItem('user', JSON.stringify(userData));
            setUser(userData);
        } catch (error) {
            console.error(error);
        }
    };

    const createPost = async (postData, token) => {
        if (!token) {
            return;
        }
        try {
            if (!postData.content || !postData.projectTitle) {
                throw new Error('Content and project title are required');
            }
            if (postData.githubLink) {
                if (typeof postData.useReadmeAsDescription !== 'boolean') {
                    throw new Error('Invalid useReadmeAsDescription value');
                }
                if (postData.useReadmeAsDescription && postData.projectDescription) {
                    throw new Error('Project description should not be provided when using README');
                }
            }
            const response = await fetch('/api/posts', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`
                },
                body: JSON.stringify(postData)
            });
            const data = await response.json();
            localStorage.setItem('post', JSON.stringify(postData));
            setPost(postData);
            return data;
        } catch (error) {
            console.error(error);
        }
    };

    const getAllPosts = async () => {
        try {
            const response = await fetch('/api/posts');
            const data = await response.json();
            return data;
        } catch (error) {
            console.error(error);
        }
    };

    const getIndividualPost = async (id) => {
        try {
            const response = await fetch(`/api/posts/${id}`);
            const data = await response.json();
            return data;
        } catch (error) {
            console.error(error);
        }
    };

    useEffect(() => {
        fetchPosts();
    }, []);

    const fetchPosts = async () => {
        try {
            const response = await fetch('/api/posts', {
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            const data = await response.json();
            setPost(data.posts);
        } catch (error) {
            console.error('There was an error!', error);
        }
    };

    const updatePost = async (newPostData, id, token) => {
        if (!token) {
            return;
        }
        try {
            const response = await fetch(`/api/posts/${id}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`
                },
                body: JSON.stringify(newPostData)
            });
            const data = await response.json();
            return data;
        } catch (error) {
            console.error(error);
        }
    };

    const deletePost = async (id, token) => {
        if (!token) {
            return;
        }
        try {
            const response = await fetch(`/api/posts/${id}`, {
                method: 'DELETE',
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });
            const data = await response.json();
            return data;
        } catch (error) {
            console.error(error)
            }
        };
    
        useEffect(() => {
            if (token) {
                const fetchUserData = async () => {
                    console.log('fetching user data');
                    try {
                        const response = await fetch('/api/user-data', {
                            headers: {
                                Authorization: `Bearer ${token}`
                            }
                        });
                        if (response.ok) {
                            const userData = await response.json();
                            setUser(userData);
                        } else {
                            throw new Error('response failed');
                        }
                    } catch (error) {
                        console.error('Error fetching user data:', error);
                    }
                };    
                fetchUserData();
            }
        }, [token]);
    
        const likePost = async (postId, token) => {
            try {
                if (!token) {
                    return;
                }
    
                const response = await fetch(`/api/posts/${postId}/like`, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${token}`
                    }
                });
    
                const data = await response.json();
                return data;
            } catch (error) {
                console.error(error);
            }
        };
    
        const unlikePost = async (postId, token) => {
            try {
                if (!token) {
                    return;
                }
    
                const response = await fetch(`/api/posts/${postId}/unlike`, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${token}`
                    }
                });
    
                const data = await response.json();
                return data;
            } catch (error) {
                console.error(error);
            }
        };
    
        const followDeveloper = async (userId, developerId, token) => {
            try {
                if (!token) {
                    return;
                }
    
                const response = await fetch('/api/follow', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${token}`
                    },
                    body: JSON.stringify({ userId, developerId })
                });
    
                const data = await response.json();
                return data;
            } catch (error) {
                console.error(error);
            }
        };
    
        const unfollowDeveloper = async (userId, developerId, token) => {
            try {
                if (!token) {
                    return;
                }
    
                const response = await fetch('/api/unfollow', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${token}`
                    },
                    body: JSON.stringify({ userId, developerId })
                });
    
                const data = await response.json();
                return data;
            } catch (error) {
                console.error(error);
            }
        };
    
        return (
            <>
                <div className={styles.App}>
                    {shouldNotDisplayNavBar && (
                        <NavBar
                            token={token}
                            setUser={setUser}
                            user={user}
                            setToken={setToken}
                        />
                    )}
                    <Routes>
                        <Route path='/' element={
                            <HomePage
                                user={user} 
                                token={token}
                                setToken={setToken}
                                setUser={setUser}
                                createPost={createPost}
                                setPost={setPost}
                                post={post}
                                getAllPosts={getAllPosts}
                            />
                        }></Route>
    
                        <Route path='/auth' element={
                            <AuthPage
                                setUser={setUser}
                                setToken={setToken}
                                signUp={signUp}
                                login={login}
                            />
                        }></Route>
                        <Route path="/auth/forgot-password" element={
                            <ForgotPassword
                                setUser={setUser}
                                setToken={setToken}
                                signUp={signUp}
                                login={login}
                            />
                        }></Route>
                        <Route path='/profile/:id' element={
                            <ProfilePage
                                user={user}
                                token={token}
                                setToken={setToken}
                                setUser={setUser}
                                getIndividualPost={getIndividualPost}
                                deletePost={deletePost}
                                updatePost={updatePost}
                            />
                        }></Route>
                    </Routes>
                </div>
            </>
        );
    }
    